<template>
  <div class="register">
    <div class="app-logo">

      <div class="logo">
        <van-uploader
          v-model="fileList"
          multiple
          :max-size="2097152"
          :max-count="1"
          @oversize="exceedimgsize()"
        />
      </div>

    </div>
    <div>
      <van-cell-group>
        <van-field
          class="trimming"
          v-model="register.account.value"
          type="text"
          label="用户名"
          placeholder="请输入用户名"
          left-icon="manager"
          :error-message="register.account.testingc ? '' : register.account.tips"
          clickable
          clearable
          maxlength="16"
          @input="testingcAcount()"
        />
        <van-field
          class="trimming"
          v-model="register.pwd.value"
          :type="register.pwd.see ? 'password' : 'text'"
          label="密码"
          placeholder="请输入密码"
          left-icon="lock"
          :right-icon="register.pwd.see ? 'closed-eye' : 'eye-o'"
          :error-message="register.pwd.testingc ? '' : register.pwd.tips"
          @click-right-icon="checkPassword()"
          clickable
          clearable
          maxlength="18"
          @input="testingcPwd()"
        />
        <van-field
          class="trimming"
          v-model="register.againPwd.value"
          :type="register.pwd.see ? 'password' : 'text'"
          label="确认密码"
          placeholder="请输入确认密码"
          left-icon="lock"
          :error-message="register.againPwd.testingc ? '' : register.againPwd.tips"
          clickable
          clearable
          maxlength="18"
          @input="testingcAgainPwd()"
        />

      </van-cell-group>
      <div class="register-form">
        <div class="HeadPortrait" v-show="fileList.length == 0">
          请上传头像
        </div>
        <van-button
          class="register-btn"
          :disabled="!refer"
          :loading="registerLoading"
          type="info"
          block
          loading-text="注册中..."
          text="注册"
          @click="Submission()"
       />
      </div>
      <div class="login">已有账号？<router-link to="/Land">登录</router-link></div>
    </div>
  </div>
</template>

<script>
  import {createNamespacedHelpers} from 'vuex'

  //基于registerModule的命名空间导入mapState、mapMutations 辅助函数

  const {mapState, mapMutations} = createNamespacedHelpers('registerModule');

  export default {
    name: 'register',

    data() {
      return {
        // 输入框信息
        // 账号
        register: {
          account: {
            value: '',
            testingc: true,
            tips: '账号格式错误,允许字母数字下划线',
            pat: /^[a-zA-Z0-9_]{4,15}$/
          },
          // 密码
          pwd: {
            value: '',
            testingc: true,
            tips: '密码以字母开头,允许包含字母、数字下划线',
            see: true,
            pat: /^[a-zA-Z]\w{5,17}$/
          },
          // 确认密码
          againPwd: {
            value: '',
            testingc: true,
            tips: '两次密码不一致,请检查后再提交！',
          }
        },

        // 上传头像图片
        fileList: [],


        // 注册加载中
        registerLoading: false,

      }
    },
    //计算属性
    computed: {
      // ...mapState(['pwd1', 'pwd2'])

      // 验证是否允许提交
      refer(){
        if(this.fileList.length == 0){
          return;
        }
        for(let i in this.register){
          if(!this.register[i].testingc || this.register[i].value.trim() == ''){
            return false;
          }
        }
        return true;
      }
    },

    //方法
    methods: {
      // ...mapMutations(['checkPassword'])

      // ...mapMutations(['startdata']),

      // 切换查看密码
      checkPassword(){
        this.register.pwd.see = !this.register.pwd.see;
      },

      // 验证用户账号
      testingcAcount(){
        let object = this.register.account;
        object.testingc = object.pat.test(object.value);
      },

      // 验证用户密码
      testingcPwd(){
        let object = this.register.pwd;
        object.testingc = object.pat.test(object.value);
        this.testingcAgainPwd();
      },

      // 验证确认密码
      testingcAgainPwd(){
        let object = this.register.againPwd;
        object.testingc = object.value === this.register.pwd.value;
      },

      // 上传图片超出限制大小
      exceedimgsize(){
        alert('所选图片大小超出2m,或者格式错误,请重新选择。')
      },

      // 提交注册信息
      Submission(){
        this.registerLoading = true;
        let obj = {
          url: this.fileList[0],
          account: this.register.account.value,
          pwd: this.register.pwd.value,
        }
				let jieguo = insertuser(obj);
        if(jieguo){
          // 成功通知
          this.$notify({
            type: 'success',
            message: '注册成功',
            duration: 1000,
            onClose: () => {
              this.$dialog.confirm({
                title: '注册成功',
                message: '是否跳转到登录页面？'
              }).then(() => {
                this.$router.push({name: 'Land'});
              }).catch(() => {
                // on cancel点击取消事件
              });
            }
          });

        }else{
          this.$notify({
            type: 'warning',
            message: '注册失败！该账户已存在',
            duration: 2000
          });
        }
        this.registerLoading = false;
      }
    },

    // 钩子
    created(){

      // console.log(this.startdata())
      // this.startdata
    }

  }
</script>

<style>
  .van-image.van-uploader__preview-image,.van-uploader__upload{
    border-radius: 50% !important;
  }
</style>

<style lang="less" scoped>
    
  .trimming{
    margin: .5rem 0;
  }
  .register{
    .app-logo{
      height: 5.333rem;
      position: relative;
      background: url(../assets/image/Starry.jpg) no-repeat center center;
      background-size: cover;
      .logo{
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        // transform: translate(-50%, -50%);
        // background-color: #ddd;
        width: 2.133rem;
        height: 2.133rem;
        // border-radius: 50%;
      }
    }

    .register-form{
      padding: 0.266rem;
    }
    .HeadPortrait{
      text-align: center;
      font-size: 0.373rem;
      line-height: 0.533rem;
      color: #ee7116;
      background-color: #fffbe8;
    }
    .register-btn{
      // margin-top: 0.533rem;
    }
    .login{
      margin-top: 0.533rem;
      text-align: center;

    }
  }
</style>
